.card-product {
	margin-top: 72px;
	display: flex;
	justify-content: space-between;
	&__box {
		&--left {
			width: 593px;
		}
		&--right {
			width: 450px;
		}
	}
	&__slider {
		height: 520px;
		width: 100%;
		background-color: $color-white;
		border-radius: 4px;
		padding: 24px 32px 40px;
		margin-bottom: 24px;
		position: relative;
		&-top {
			margin-bottom: 33px !important;
			.slick-dots {
				bottom: -6px;
				li {
					width: 13px;
					height: 13px;
					button {
						width: 10px;
						height: 10px;
						background-color: rgba($color-main2, 0.3);
						border-radius: 50%;
						padding: 0;
						border: 1px solid $color-white;
						&::before {
							content: '';
							width: 6px;
							height: 6px;
							top: 2px;
							left: 2px;
							border-radius: 50%;
						}
						&:hover {
							background-color: rgba($color-main2, 0.6);
						}
					}
				}
				li.slick-active {
					button {
						background-color: $color-white;
						border: 1px solid rgba($color-main2, 0.8);
						&::before {
							background-color: rgba($color-main2, 1);
						}
					}
				}
			}
			.main-slider__arrow--prev {
				left: -18.5px;
			}
			.main-slider__arrow--next {
				right: -18.5px;
			}
		}
	}
	&__item-top {
		height: 348px;
		display: flex !important;
		justify-content: center;
		align-items: center;
		img {
			max-width: 529px;
			max-height: 348px;
		}
	}
	&__item-bottom {
		width: 76px;
		height: 76px;
		box-sizing: border-box;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 6px;
		border: 1px solid rgba($color-main2, 0.3);
		outline: none;
		cursor: pointer;
		transition: border-color .3s;
		&:hover {
			border: 1px solid rgba($color-main2, 0.6);
		}
		img {
			max-width: 74px;
			max-height: 74px;
		}
	}
	&__item-bottom.slick-active.slick-slide.slick-current {
		border: 1px solid $color-blue5;
	}
	&__arrow {
		position: absolute;
		top: 28px;
		width: 96px;
		height: 20px;
		background-color: $color-white;
		z-index: 4;
		display: flex;
		align-items: center;
		cursor: pointer;
		box-shadow: 0 4px 3px -3px rgba(19, 18, 30, 0.16);
		svg {
			width: 14px;
			height: 8px;
			opacity: 0.35;
			transition: opacity .3s, fill .3s;
		}
		&--prev {
			left: -42px;
			justify-content: center;
			transform: rotate(-90deg);
		}
		&--next {
			right: -41px;
			justify-content: center;
			transform: rotate(90deg);
		}
		&:hover {
			svg {
				opacity: 1;
				fill: $color-blue5;
			}
		}
	}
}
.main-slider__arrow.slick-disabled {
	opacity: 0;
	visibility: hidden;
}
.card-product__arrow.slick-disabled {
	opacity: 0;
	visibility: hidden;
}

.card-product {
	&__desc {
		background-color: $color-white;
		border-radius: 4px;
		border: 1px solid $color-dark-bg;
	}
	&__desc-top {
		border-bottom: 1px solid $color-dark-bg;
	}
	&__tab {
		display: inline-block;
		font-family: $font-firaM;
		font-size: 16px;
		font-weight: 500;
		letter-spacing: 0.6px;
		color: $color-blue5;
		padding: 25px 5px 0 24px;
		&-span {
			display: block;
			padding-bottom: 11px;
		}
		&--active {
			color: $color-main2;
			.card-product__tab-span {
				border-bottom: 4px solid $color-blue5;
			}
		}
	}
	&__desc-wrap {
		display: none;
		&--active {
			display: block;
		}
	}
	&__desc-bottom {
		padding: 29px 24px 39px;
	}
	&__tr td{
		padding-bottom: 15px;
		padding-right: 30px;
	}
	&__par {
		font-size: 16px;
		line-height: 1.5;
		color: $color-main2;
		margin: 0 0 18px;
	}
	&__par-gray {
		margin: 0;
		font-size: 14px;
		line-height: 1.43;
		color: #5f5f67;
	}
	&__block {
		display: none;
		&--active {
			display: block;
		}
	}
	&__review {
		&-head {
			display: flex;
			justify-content: space-between;
		}
		&-title {
			font-family: $font-firaM;
			font-size: 18px;
			font-weight: 500;
			line-height: 1.33;
		}
		&-btn {
			padding-top: 0;
			padding-bottom: 0;
		}
		&-item {
			padding-top: 18px;
			padding-bottom: 21px;
			border-top: 1px solid rgba($color-main2, 0.3);
			&:first-child {
				border-top: none;
			}
		}
		&-name {
			display: inline-block;
			margin-right: 18px;
			font-family: $font-firaM;
			font-size: 16px;
			font-weight: 500;
			line-height: 1.5;
		}
		&-date {
			display: inline-block;
			font-size: 16px;
			font-style: italic;
			line-height: 1.5;
			color: #626260;
		}
		&-desc {
			display: block;
			margin-bottom: 4px;
			font-size: 16px;
			line-height: 1.5;
			color: #292930;
		}
		&-foot {
			background-color: $color-white;
			width: 100%;
			margin-bottom: 5px;
		}
	}
	&__link {
		font-size: 15px;
		display: flex;
		align-items: center;
		span {
			margin-left: 7px;
		}
	}
	&__form {
		.label {
			margin-top: 24px;
		}
		.italic {
			padding-left: 126px;
			margin-top: 10px;
			margin-bottom: 26px;
		}
		.btn {
			margin-left: 126px;
			width: 129px;
			height: 40px;
		}
	}
	&__mass {
		text-align: center;
		padding-top: 57px;
		padding-bottom: 95px;
		&-svg {
			margin: 0 auto 17px;
			width: 40px;
			height: 40px;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: $color-green;
			border-radius: 50%;
		}
		&-title {
			font-family: $font-firaM;
			font-size: 22px;
			font-weight: 500;
			line-height: 1.09;
			color: $color-main2;
			margin-bottom: 9px;
		}
		&-desc {
			font-size: 18px;
			line-height: 1.33;
			margin-bottom: 22px;
		}
	}
	&__filter {
		border-radius: 4px;
		box-shadow: 0 4px 18px 0 rgba(217, 211, 189, 0.49);
		background-color: $color-white;
		border: 1px solid $color-dark-bg;
		padding: 34px 24px 43px;
	}
}